@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.screen-background {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 16px 0;
}

.intro-background {
	display: flex;
	flex-direction: row;
}

.intro-item {
  
  display: block;
  flex: 1 1;
  padding: 4px;
  
}

.intro-item h1,
h2,
ul,
li {
  margin: 4px 0;
  text-align: center;
}

.intro-item p {
  margin: 10px;
  text-align: center;
}

.intro-picture {
  width: 100%;
  height: 100%;
}

.educationy-background {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 160px;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    color: black;
    padding: 16px 0;
  }
  
  .educationy-background:hover {
    transform: translateX(8px);
    color: #480952;
    background-image: linear-gradient(to left, white, #fffff0);
  }
  
  .educationy-image {
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
  }
  
  .educationy-info {
    flex: 2 1;
    padding: 16px;
  }
  
.experience-background {
  display: flex;
}

.timeline-background {
  width: 50%;
}

.timeline-item {
  display: flex;
  width: 50%;
  height: 120px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.timeline-item:hover {
  transform: scale(1.2);
  background-color: rgb(226, 230, 196);
}

.timeline-ball {
  position: absolute;
  width: 24px;
  height: 24px;
  border-radius: 12px;
  background: rgba(206, 201, 202, 0.363);
}

.experience-info {
  padding: 24px;
  font-size: 10pt;
}

.experience-info h4,
p {
  margin: 0;
}

.highlight-background {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: calc(100%);
  background: rgba(211, 202, 204, 0.274);
  position: -webkit-sticky;
  position: sticky;
  top: 48px;
  justify-content: center;
  align-items: center;
  margin: 32px;
}

.highlight-background h1,
h4,
p {
  margin: 8px;
}

.project-background {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 160px;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  color: black;
  padding: 16px 0;
}

.project-background:hover {
  transform: translateX(8px);
  color: #480952;
  background-image: linear-gradient(to left, white, #fffff0);
}

.project-image {
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
}

.project-info {
  flex: 2 1;
  padding: 16px;
}

.info {
  display: flex;
  flex-direction: column;
  width: 60%;
  align-items: center;
  margin: 16px 0px;
}

.info-bar {
  display: flex;
  flex-direction: row;
  width:100%;
  justify-content: space-around;
}

.info-item {
  display:flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-around;
  margin: 8px 0px;
}

.info-link {
  display: flex;
  margin: 0;
  justify-content: center;
  align-items: center;
  color: #666666;
  text-decoration: none;
  font-weight: 400;
}

.info-link:hover {
  color: black;
  transform: scale(1.2);
}

.nav-background {
  display: flex;
  flex-direction: row;
  border-top: 4px solid black;
  border-bottom: 4px solid black;
  width: 60%;
  justify-content: space-around;
}

.nav-item {
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
}

.nav-link {
  text-decoration: none;
  font-weight: 500;
  color: gray;
  transition: all 0.2s ease-in-out;
}
.nav-link:hover {
  color: black;
  transform: sclae(1.2);
}

